.bg {
  margin: 0 auto;
  background-color: lightpink;
  max-width: 450px;
}

.cover {
  display: flex;
  position: fixed;
  top: 0px;
  flex-direction: column;
  align-items: center;
  z-index: 11;
  background-image: linear-gradient(185deg, rgba(0, 0, 0, 0.69) 9%, rgba(0, 0, 0, 0) 100%), url('../pic/RS_7.jpg');
  background-position: 40% 30%;
  width: 100%;
  max-width: 450px;
  height: 100vh;
}

.move {
  top: -100vh;
  transition: all 1s;
}

.button {
  cursor: pointer;
  margin-top: 30px;
  border: none;
  border-radius: 20px;
  width: 160px;
  height: 35px;
  font-size: 16px;
}

.button::after {
  -webkit-transition: all .2s;
  -webkit-font-feature-settings: "kern" off;
  font-feature-settings: "kern" off;
  opacity: 0;
  transition: all .2s;
  transition: all 300ms ease 0ms;
  margin-left: -1em;
  content: ">";
  font-style: normal;
  font-variant: none;
  font-weight: 400;
  font-size: 18px;
  line-height: 1em;
  text-shadow: none;
  text-transform: none;
}

.button:hover::after {
  opacity: 1;
  margin-left: 10px;
  width: 170px;
}

.cover_open {
  position: relative;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.42) 0%, rgba(0, 0, 0, 0) 100%), url('../pic/RS_1.jpg');
  background-position: 50%;
  background-size: cover;
  background-color: #fff;
  padding-top: 80px;
  padding-right: 0px;
  padding-bottom: 750px;
  padding-left: 0px;
}

.paper {
  margin: 50px 30px;
  box-shadow: 0px 5px 20px -10px #000000;
  border-width: 0px;
  border-color: #fbd2da;
  border-radius: 15px 15px 15px 15px;
  background-color: #fffaf6;
  padding: 80px 0px;
  overflow: hidden;
}

.img {
  filter: opacity(0.5);
  width: 150px;
}

.text {
  padding: 25px 45px 0px 45px;
  font-size: 15px;
  line-height: 2em;
}

.page3 {
  background-color: white;
  padding: 70px 50px;
}

.profile {
  margin: auto;
  margin-top: 80px;
  margin-bottom: 50px;
  box-shadow: 0px 5px 20px -10px #000000;
  border-radius: 50px;
  width: 250px;
  height: 350px;
}

.R {
  background-image: url('../pic/R.jpg');
  background-position: center;
  background-size: cover;
}

.S {
  background-image: url('../pic/S.jpg');
  background-position: center;
  background-size: cover;
}

.opacity0 {
  opacity: 0;
}

.buttonPlayPause {
  position: fixed;
  right: 10px;
  bottom: 10px;
  color: white;
  font-size: 30px;
}