/* Fade In */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.fadeIn {
  animation: fadeIn 2s ease-in;
}

/* Fade Out */
@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

.fadeOut {
  animation: fadeOut 2s ease-out;
}

/* Slide In from Left */
@keyframes slideInLeft {
  from { transform: translateX(-100%); }
  to { transform: translateX(0); }
}

.slideInLeft {
  animation: slideInLeft 1.5s ease-out;
}

/* Slide In from Right */
@keyframes slideInRight {
  from { transform: translateX(100%); }
  to { transform: translateX(0); }
}

.slideInRight {
  animation: slideInRight 1.5s ease-out;
}

/* Slide Out to Left */
@keyframes slideOutLeft {
  from { transform: translateX(0); }
  to { transform: translateX(-100%); }
}

.slideOutLeft {
  animation: slideOutLeft 1s ease-in;
}

/* Zoom In */
@keyframes zoomIn {
  from { transform: scale(0.5); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

.zoomIn {
  animation: zoomIn 1s ease-out;
}

/* Zoom Out */
@keyframes zoomOut {
  from { transform: scale(1); opacity: 1; }
  to { transform: scale(0.5); opacity: 0; }
}

.zoomOut {
  animation: zoomOut 1s ease-in;
}

/* Bounce */
@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-30px); }
}

.bounce {
  animation: bounce 2s infinite;
}

/* Pulse */
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.pulse {
  animation: pulse 1.5s infinite;
}

/* Shake */
@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  50% { transform: translateX(5px); }
  75% { transform: translateX(-5px); }
}

.shake {
  animation: shake 0.5s ease-in-out;
}

/* Rotate */
@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.rotate {
  animation: rotate 2s linear infinite;
}

/* Swing */
@keyframes swing {
  15% { transform: rotate(15deg); }
  30% { transform: rotate(-10deg); }
  45% { transform: rotate(5deg); }
  60% { transform: rotate(0); }
}

.swing {
  animation: swing 1s ease-out;
}

/* Tada */
@keyframes tada {
  0%, 100% { transform: scale(1); }
  10%, 20% { transform: rotate(-10deg); }
  30%, 50%, 70% { transform: rotate(10deg); }
  40%, 60%, 80% { transform: rotate(-10deg); }
}

.tada {
  animation: tada 1s ease;
}

/* Flip */
@keyframes flip {
  from { transform: rotateY(0deg); }
  to { transform: rotateY(180deg); }
}

.flip {
  animation: flip 1s ease-in-out;
}

/* Light Speed In */
@keyframes lightSpeedIn {
  from { transform: translateX(100%) skewX(-30deg); opacity: 0; }
  to { transform: translateX(0) skewX(0); opacity: 1; }
}

.lightSpeedIn {
  animation: lightSpeedIn 1s ease-out;
}

/* Wobble */
@keyframes wobble {
  0%, 100% { transform: translateX(0); }
  15% { transform: translateX(-25px); }
  30% { transform: translateX(20px); }
  45% { transform: translateX(-15px); }
  60% { transform: translateX(10px); }
  75% { transform: translateX(-5px); }
}

.wobble {
  animation: wobble 1s ease-in-out;
}

/* Flash */
@keyframes flash {
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
}

.flash {
  animation: flash 1s infinite;
}

/* Bounce In */
@keyframes bounceIn {
  0% { transform: scale(0); opacity: 0; }
  60% { transform: scale(1.2); opacity: 1; }
  100% { transform: scale(1); }
}

.bounceIn {
  animation: bounceIn 1s ease-out;
}

